import React from "react";
import { Form, Button, Container, Col, Row } from "react-bootstrap";
import NavBarR from "../components/NavBarR";

const Register = () => {
  return (
    <div>
      <NavBarR />
      <Container className="mt-5">
        <div className="text-center"> REGISTRO DE NUEVO USUARIO </div>
        <Form className="mt-2">
          <Form.Group className="mb-3" controlId="formGridName">
            <Form.Label>Nombre completo</Form.Label>
            <Form.Control placeholder="Ingrese nombres y apellidos aquí" />
          </Form.Group>

          <Form.Group className="mb-3" controlId="formGridAddress">
            <Form.Label>Dirección</Form.Label>
            <Form.Control placeholder="Dirección con calle, manzana, etc..." />
          </Form.Group>

          <Row className="mb-3">
            <Form.Group as={Col} controlId="formGridEmail">
              <Form.Label>Email</Form.Label>
              <Form.Control type="email" placeholder="Ingrese el email" />
            </Form.Group>

            <Form.Group as={Col} controlId="formGridPhone">
              <Form.Label>Celular</Form.Label>
              <Form.Control
                type="Phone"
                placeholder="Celular o teléfono de contacto"
              />
            </Form.Group>
          </Row>

          <Form.Group className="mt-5" as={Col} controlId="formGridUserName">
            <Form.Label>Nombre de usuario</Form.Label>
            <Form.Control />
          </Form.Group>

          <Row className="mb-3">
            <Form.Group className="mt-3" as={Col} controlId="formGridPassword">
              <Form.Label>Contraseña</Form.Label>
              <Form.Control type="password" placeholder="Contraseña" />
            </Form.Group>

            <Form.Group className="mt-3" as={Col} controlId="formGridPassword">
              <Form.Label>Confirmar contraseña</Form.Label>
              <Form.Control
                type="password"
                placeholder="Confirme su contraseña"
              />
            </Form.Group>
          </Row>

          <Row>
            <Col> </Col>
            <Col>
              <Button
                className="mt-5"
                variant="primary"
                type="cancel"
                size="lg"
              >
                Cancelar
              </Button>
            </Col>

            <Col>
              <Button className="mt-5" variant="primary" type="save" size="lg">
                Guardar
              </Button>
            </Col>
          </Row>
        </Form>
      </Container>
    </div>
  );
};

export default Register;
